import React, { useRef, RefObject } from 'react';
import Footer from '../components/footer';
import styles from '../styles/pages/contact.module.scss';

const Contact: React.FC = () => {

  const nameInput = useRef(null) as RefObject<HTMLInputElement>;
  const emailInput = useRef(null);
  const messageInput = useRef(null);

  const onSubmit = () => {

  }

  return(
    <div className={styles.animated}>

      <div className={styles.body}>
        <div className={styles.title}>Lets get in touch!</div>
        <div className={styles.subtext}>
          Thanks for stopping by! If you've got any inquiries, or you just want to
          say hello, feel free to send me a message. I'll try to get back to you
          as quickly as possible.
        </div>

        <iframe 
          name="hidden_iframe" 
          style={{display:'none'}} 
        ></iframe>

        <form 
          action='https://docs.google.com/forms/u/1/d/e/1FAIpQLSfHtS1qNBlX-53WYeCwY7ohLf1rZ0Qrkas61GCsp9l46RacFQ/formResponse'
          method="post" 
          target="hidden_iframe"
          onSubmit={() => {onSubmit()}}
        >
          <input 
            name="entry.67477018"
            type="text" 
            placeholder="Name"
            required
            ref={nameInput}
          />
          <div className={styles.separator}/>
          <input 
            name="entry.658136846"
            type="email" 
            placeholder="Email"
            required
            ref={emailInput}
          />
          <textarea 
            name="entry.1078839895"
            placeholder="Message"
            required
            ref={messageInput}
          />
          <input className={styles.submit} type="submit" value="Submit"/>
        </form> 

        <Footer/>
        
      </div>
        
    </div>

  );
}

export default Contact;